.productList {
  padding: 50px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.productList-texts {
  width: 65%;
}

.productList-title {
  font-size: 50px;
  font-weight: 600;
}

.productList-desc {
  margin: 20px 0px;
}

.productList-list {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.project-title {
  width: 100%;
  display: flex;
  justify-content: left;
  margin-left: 70px;
  margin-top: 30px;
}

@media screen and (max-width: 480px) {
  .productList {
    padding: 10px;
    margin-top: 70px;
  }
  .productList-texts {
    width: 100%;
  }
  .productList-desc {
    display: none;
  }
  @media screen and (max-height: 670px) {
    .productList {
      margin-top: 250px;
    }
  }
}
