.toggle {
  width: 50px;
  height: 25px;
  border-radius: 20px;
  border: 2px solid grey;
  background-color: white;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.toggle-icon {
  width: 15px;
  height: 15px;
}

.toggle-button {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: grey;
  position: absolute;
  cursor: pointer;
  border: 2px solid #333;
}
