.about {
  height: 100vh;
  display: flex;
  align-items: center;
}

.about-right {
  flex: 1;
  padding-right: 13px;
}

.about-left {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 100%;
}

.about-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}

.about-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #333;
}

.about-award-img {
  height: 40px;
  width: 40px;
  margin: 0 2%;
  transition: transform 250ms;
  transition: all 0.3s ease-in-out;
}

.about-award-img:hover {
  transition-duration: 0.3s;
  transform: scale(1.2);
}

.about-title {
  font-weight: 400;
}

.about-subtitle {
  margin: 20px 0;
}

.about-description {
  font-weight: 300;
}

.about-tech-text {
  margin-top: 30px;
  margin-bottom: 15px;
  color: #555;
}

.about-tech {
  width: 51.5%;
  height: 50px;
  display: flex;
  border-radius: 15px;
  align-items: center;
  overflow: hidden;
  flex-wrap: wrap;
  justify-content: center;
}

@media screen and (max-width: 480px) {
  .about {
    flex-direction: column;
    text-align: center;
    margin-top: 15px;
  }

  .about-left {
    width: 100%;
  }
  .about-card {
    height: 30vh;
  }
  .about-card.bg {
    display: none;
  }

  .about-right {
    padding: 20px;
  }
  .about-tech {
    width: fit-content;
    height: fit-content;
    margin: auto;
  }
}
