.product {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;
  border: 2px solid rgb(243, 242, 242);
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.product-browser {
  height: 20px;
  background-color: rgb(243, 242, 242);
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 2;
}

.product-circle {
  width: 6px;
  height: 6px;
  background-color: white;
  border-radius: 50%;
  margin: 3px;
}

.product-img {
  width: 100%;
  transition: all 10s ease;
}

.product:hover .product-img:hover {
  transform: translateY(-40%);
}

.omdb {
  transition: none;
  transform: none;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 480px) {
  .product {
    width: 40%;
    height: 20vh;
  }
  .project-title {
    margin: auto;
  }
  .productList-list {
    justify-content: center;
  }
}
