.intro {
  display: flex;
  height: 100vh;
}

.intro-left {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro-right {
  flex: 1;
  position: relative;
}

.intro-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.i-intro {
  font-size: 30px;
  font-weight: 300;
  animation: backInDown;
  animation-duration: 2s;
}

.i-name {
  font-size: 60px;
  animation: backInDown;
  animation-duration: 2s;
}

.i-title {
  animation: backInDown;
  animation-duration: 2s;
  height: 50px;
  overflow: hidden;
}

.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
  50% {
    transform: translateY(-50px);
  }
  100% {
    transform: translateY(-100px);
  }
}

.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-description {
  animation: fadeInLeft;
  animation-duration: 2s;
}

.i-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
}

.intro-bg {
  clip-path: polygon(100% 0%, 100% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: black;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (max-width: 480px) {
  .intro {
    flex-direction: column;
  }
  .intro-left-wrapper {
    padding: 10px;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
    margin-bottom: -80px;
  }
  .i-description {
    display: none;
  }

  .i-title-item {
    display: flex;
    justify-content: center;
    font-size: 25px;
  }
  @media screen and (max-width: 320px) {
    .i-title-item {
      font-size: 20px;
    }
  }
  @media screen and (max-height: 670px) {
    .intro-right {
      margin-top: 50px;
    }
  }
}
